<template>
  <CChartBar :datasets="defaultDatasets" :labels="month" :options="defaultOptions" />
</template>

<script>
import { CChartBar } from '@coreui/vue-chartjs';

export default {
  name: 'SupportTicketBar',
  data() {
    return {
      month: '',
    };
  },
  components: { CChartBar },
  computed: {
    defaultDatasets() {
      return [
        {
          label: 'GitHub Commits',
          backgroundColor: '#ff9f4066',
          borderColor: '#ff9f40',
          borderWidth: 1,
          hoverBorderWidth: 2,
          data: [0, 0, 0, 5, 7, 2],
        },
      ];
    },
    defaultOptions() {
      return {
        reponsive: true,
        maintainAspectRatio: false,
        aspectRatio: 1,
        legend: {
          display: false,
        },
        scales: {
          xAxes: [
            {
              // bar寬度
              barPercentage: 0.5,
              // 直線
              gridLines: {
                drawOnChartArea: true,
              },
            },
          ],
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                maxTicksLimit: 11,
                stepSize: 5,
                max: 30,
                min: 0,
              },
              // 橫線
              gridLines: {
                display: true,
              },
            },
          ],
        },
      };
    },
  },
  mounted() {
    this.month = this.common.getSixMonth();
  },
};
</script>
